.mask{
width: 100%;
height: 100vh;
position: relative;
}
.intro-img{
width: 100%;
height: 100%;
background-size: cover;
object-fit: cover;
background-position: center;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.7;
}
.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content h1{
    font-size: 4rem;
    padding:0.6rem 0 1.5rem;
}
.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}
.hero .content .btn{
    margin-right: 5px;
}
.avater{
    width: 300px;
    border-radius: 50%;
    margin-top: 100px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    margin-bottom: 30px;
}

@media screen and (max-width:640px){
    .hero .content h1{
        font-size: 3rem;    
    }
    .hero .content p{
        font-size: 1.4rem;
    }
   
}